<script setup>
import { ref, onMounted } from 'vue'
import { onShow } from '@dcloudio/uni-app'
import { api } from '@/utils/api.js'

const topTab = ref('square')
const currentUser = ref(null)
const searchText = ref('')
const isSearching = ref(false)

// Âä®ÊÄÅÈ°∂ÈÉ®ÂÜÖËæπË∑ù
const dynamicTopPadding = ref('')
onMounted(async () => {
	try {
		const info = uni.getSystemInfoSync()
		const statusBar = info.statusBarHeight || 0
		const screenW = info.screenWidth || 375
		const rpxToPx = (rpx) => (rpx * screenW) / 750
		const topPx = Math.round(statusBar * 0.35)
		dynamicTopPadding.value = `padding-top:${topPx}px;`
	} catch (e) {
		dynamicTopPadding.value = ''
	}
	
	// Ê∏ÖÁêÜÂõæÁâáÁºìÂ≠òÔºàÂè™Âú®Â∫îÁî®ÂêØÂä®Êó∂Ê∏ÖÁêÜ‰∏ÄÊ¨°Ôºâ
	postImageCache.clear()
	avatarCache.clear()
	imageCache.clear()
	
	// Ëé∑ÂèñÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ
	try {
		const userProfile = await api.getProfile()
		currentUser.value = userProfile
	} catch (e) {
		console.error('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', e)
	}
	
	// ÂàùÊ¨°ËøõÂÖ•Âä†ËΩΩÂπøÂú∫Êï∞ÊçÆ
	loadFeeds()
	// Âä†ËΩΩÈóÆÁ≠îÊï∞ÊçÆÔºàÂú®Áî®Êà∑‰ø°ÊÅØÂä†ËΩΩÂÆåÊàêÂêéÔºâ
	loadQuestions()
	// ÁõëÂê¨Âà∑Êñ∞‰∫ã‰ª∂
	try { uni.$on('feeds:refresh', () => { if (topTab.value === 'square') loadFeeds() }) } catch (e) {}
	// ÁõëÂê¨ÈóÆÁ≠îÂà∑Êñ∞‰∫ã‰ª∂
	try { uni.$on('qa:refresh', () => { if (topTab.value === 'qa') loadQuestions() }) } catch (e) {}
})

// Áªü‰∏ÄÁöÑÊó∂Èó¥Ê†ºÂºèÂåñÂáΩÊï∞
function formatRelativeTime(createdAt) {
	const now = new Date()
	
	// Ëß£ÊûêcreatedAt (Ê†ºÂºè: 2025-10-23T19:56:29.000Z)
	const createdDate = createdAt.split('T')[0] // 2025-10-23
	const createdTime = createdAt.split('T')[1].split('.')[0] // 19:56:29
	const [createdYear, createdMonth, createdDateNum] = createdDate.split('-').map(Number)
	const [createdHour, createdMinute] = createdTime.split(':').map(Number)
	
	// Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥ÁöÑÂπ¥ÊúàÊó•Êó∂ÂàÜÁßí
	const nowYear = now.getFullYear()
	const nowMonth = now.getMonth() + 1
	const nowDate = now.getDate()
	const nowHour = now.getHours()
	const nowMinute = now.getMinutes()
	
	// ËÆ°ÁÆóÊÄªÂàÜÈíüÂ∑ÆÔºàÂü∫‰∫éÂπ¥ÊúàÊó•Êó∂ÂàÜÔºâ
	const totalMinutesDiff = (nowYear - createdYear) * 365 * 24 * 60 + 
		(nowMonth - createdMonth) * 30 * 24 * 60 + 
		(nowDate - createdDateNum) * 24 * 60 + 
		(nowHour - createdHour) * 60 + 
		(nowMinute - createdMinute)
	
	// Âà§Êñ≠ÊòØÂê¶Ë∂ÖËøá24Â∞èÊó∂Ôºà1440ÂàÜÈíüÔºâ
	if (totalMinutesDiff < 1440) {
		// 24Â∞èÊó∂ÂÜÖÔºåÊòæÁ§∫Áõ∏ÂØπÊó∂Èó¥
		if (totalMinutesDiff < 1) {
			return 'ÂàöÂàö'
		} else if (totalMinutesDiff < 60) {
			return `${totalMinutesDiff}ÂàÜÈíüÂâç`
		} else {
			const hours = Math.floor(totalMinutesDiff / 60)
			return `${hours}Â∞èÊó∂Ââç`
		}
	} else {
		// Ë∂ÖËøá24Â∞èÊó∂ÔºåÊòæÁ§∫ÂàõÂª∫Êó•ÊúüÊó∂Èó¥
		return `${createdMonth}/${createdDateNum} ${createdHour.toString().padStart(2,'0')}:${createdMinute.toString().padStart(2,'0')}`
	}
}

// È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
onShow(() => {
	// Â¶ÇÊûúÂΩìÂâçÂú®ÈóÆÁ≠îÊ†áÁ≠æÈ°µÔºåÂà∑Êñ∞ÈóÆÁ≠îÊï∞ÊçÆ
	if (topTab.value === 'qa') {
		loadQuestions()
	}
	// Â¶ÇÊûúÂΩìÂâçÂú®ÂπøÂú∫Ê†áÁ≠æÈ°µÔºåÂà∑Êñ∞Âä®ÊÄÅÊï∞ÊçÆ
	else if (topTab.value === 'square') {
		loadFeeds()
	}
	// Â¶ÇÊûúÂΩìÂâçÂú®ÁßëÊôÆÊ†áÁ≠æÈ°µÔºåÂà∑Êñ∞ÁßëÊôÆÊï∞ÊçÆ
	else if (topTab.value === 'science') {
		loadArticles()
	}
})
const categories = ref([
	{ key: 'rec', name: 'Êé®Ëçê' },
	{ key: 'daily', name: 'ÁîüÊ¥ªÊó•Â∏∏' },
	{ key: 'dress', name: 'ÂÆ†Áâ©Á©øÊê≠' },
	{ key: 'care', name: 'ÂÖªÊä§ÂàÜ‰∫´' },
	{ key: 'fun', name: 'ÊêûÁ¨ëÊó•Â∏∏' }
])
const currentCategory = ref('rec')

const posts = ref([])
// ÂπøÂú∫ÂàÜÈ°µÁä∂ÊÄÅ
const feedPage = ref(1)
const feedLimit = ref(10)
const feedHasMore = ref(true)
const feedLoading = ref(false)

async function loadFeeds(params = {}, isLoadMore = false) {
	if (feedLoading.value) return
	
	try {
		feedLoading.value = true
		const currentPage = isLoadMore ? feedPage.value : 1
		const res = await api.getFeeds({ 
			page: currentPage, 
			limit: feedLimit.value, 
			...params 
		})
		const list = Array.isArray(res) ? res : (res.feeds || res.data || [])
		
		const newPosts = list.map((f) => {
			const user = f.User || {}
			const pet = f.Pet || {}
			const imgs = Array.isArray(f.images) ? f.images : []
			
			// Êó∂Èó¥Ê†ºÂºèÂåñ
			let time = 'ÂàöÂàö'
			if (f.createdAt) {
				time = formatRelativeTime(f.createdAt)
			}
			
			// ÊèêÂèñÊ†áÈ¢òÔºà‰ªétagsÂ≠óÊÆµ‰∏≠Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™Ê†áÁ≠æ‰Ωú‰∏∫Ê†áÈ¢òÔºâ
			let title = ''
			if (f.tags && Array.isArray(f.tags) && f.tags.length > 0) {
				title = f.tags[0]
			} else if (f.tags && typeof f.tags === 'string') {
				try {
					const parsedTags = JSON.parse(f.tags)
					if (Array.isArray(parsedTags) && parsedTags.length > 0) {
						title = parsedTags[0]
					}
				} catch (e) {
					// Â¶ÇÊûúËß£ÊûêÂ§±Ë¥•ÔºåÂøΩÁï•
				}
			}
			
			return {
				id: f.id,
				userId: f.userId, // Ê∑ªÂä†Âä®ÊÄÅ‰ΩúËÄÖID
				user: user.nickname || 'Áî®Êà∑',
				pet: pet.name || '',
				breed: pet.breed || '',
				time,
				title: title ? `#${title}` : '',
				text: f.text || '',
				avatar: user.avatarUrl || '/static/404.png',
				images: imgs,
				likes: f.likes || 0,
				comments: typeof f.commentsCount === 'number' ? f.commentsCount : (Array.isArray(f.Comments) ? f.Comments.length : 0),
				shares: f.shares || 0,
				isOwner: currentUser.value && f.userId === currentUser.value.id, // Âà§Êñ≠ÊòØÂê¶‰∏∫‰ΩúËÄÖ
				isLiked: f.isLiked || false // Ê∑ªÂä†ÁÇπËµûÁä∂ÊÄÅ
			}
		})
		
		if (isLoadMore) {
			// Âä†ËΩΩÊõ¥Â§öÔºöËøΩÂä†Âà∞Áé∞ÊúâÂàóË°®
			posts.value = [...posts.value, ...newPosts]
			feedPage.value++
		} else {
			// È¶ñÊ¨°Âä†ËΩΩÊàñÂà∑Êñ∞ÔºöÊõøÊç¢ÂàóË°®
			posts.value = newPosts
			feedPage.value = 2 // ‰∏ãÊ¨°Âä†ËΩΩÊõ¥Â§öÊó∂‰ªéÁ¨¨2È°µÂºÄÂßã
		}
		
		// Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
		feedHasMore.value = newPosts.length >= feedLimit.value
		
	} catch (e) {
		if (!isLoadMore) {
			posts.value = []
		}
	} finally {
		feedLoading.value = false
	}
}

// Âä†ËΩΩÊõ¥Â§öÂπøÂú∫Âä®ÊÄÅ
async function loadMoreFeeds() {
	if (!feedHasMore.value || feedLoading.value) return
	await loadFeeds({}, true)
}

// Âä†ËΩΩÈóÆÁ≠îÊï∞ÊçÆ
async function loadQuestions(params = {}, isLoadMore = false) {
	if (qaLoading.value) return
	
	try {
		qaLoading.value = true
		const currentPage = isLoadMore ? qaPage.value : 1
		const res = await api.getQuestions({ 
			page: currentPage, 
			limit: qaLimit.value, 
			...params 
		})
		const list = Array.isArray(res) ? res : (res.questions || res.data || [])
		
		// Â§ÑÁêÜÈóÆÁ≠îÊï∞ÊçÆ
		const newQaPosts = list.map(q => {
			
			// Êó∂Èó¥Ê†ºÂºèÂåñ
			let time = 'ÂàöÂàö'
			if (q.createdAt) {
				const created = new Date(q.createdAt)
				const month = created.getUTCMonth() + 1
				const date = created.getUTCDate()
				const hours = created.getUTCHours().toString().padStart(2, '0')
				const minutes = created.getUTCMinutes().toString().padStart(2, '0')
				time = `${month}/${date} ${hours}:${minutes}`
			}
			
			// Â§ÑÁêÜÊ†áÁ≠æ
			let tags = []
			if (q.tags) {
				try {
					tags = typeof q.tags === 'string' ? JSON.parse(q.tags) : q.tags
					if (!Array.isArray(tags)) {
						tags = []
					}
				} catch (e) {
					tags = []
				}
			}
			
			const processedQ = {
				id: q.id,
				userId: q.user?.id || q.userId, // ‰ªéuserÂØπË±°‰∏≠Ëé∑ÂèñuserId
				title: q.title,
				isUrgent: q.isUrgent,
				hasAnswer: q.answerCount > 0,
				topAnswer: q.topAnswerId ? {
					id: q.topAnswerId,
					content: q.topAnswerContent,
					likes: q.topAnswerLikes || 0,
					isTopLiked: true,
					user: q.topAnswerUserId ? {
						id: q.topAnswerUserId,
						nickname: q.topAnswerUserNickname,
						avatarUrl: q.topAnswerUserAvatar
					} : null,
					pet: q.topAnswerPetName ? {
						name: q.topAnswerPetName,
						breed: q.topAnswerPetBreed
					} : null
				} : null,
				answerCount: q.answerCount || 0,
				readCount: q.views || 0,
				time: time,
				tags: tags,
				isOwner: currentUser.value && (q.user?.id || q.userId) === currentUser.value.id // Âà§Êñ≠ÊòØÂê¶‰∏∫‰ΩúËÄÖ
			}
			
			return processedQ
		})

		// ÊáíÂä†ËΩΩË°•ÈΩêÔºöÊúâÂõûÁ≠î‰ΩÜÁº∫Â∞ëÁΩÆÈ°∂ÂõûÁ≠îËØ¶ÊÉÖÊó∂ÔºåÊãâÂèñÈóÆÈ¢òËØ¶ÊÉÖÂ°´ÂÖÖ
		for (const qa of newQaPosts) {
			if (!qa.topAnswer) {
				try {
					const detail = await api.getQuestion(qa.id)
					if (detail && Array.isArray(detail.answers) && detail.answers.length > 0) {
						const top = [...detail.answers].sort((a, b) => (b.likes - a.likes) || (new Date(a.createdAt) - new Date(b.createdAt)))[0]
						qa.topAnswer = {
							id: top.id,
							content: top.content,
							likes: top.likes || 0,
							isTopLiked: true,
							user: top.user || null,
							pet: top.pet || null
						}
					} else {
						// Êó†ÂõûÁ≠îÔºå‰∏çËøõË°åÂ°´ÂÖÖ
					}
				} catch (err) {
					// ÂøΩÁï•Â°´ÂÖÖÈîôËØØÔºå‰øùÊåÅÂéüÊï∞ÊçÆ
				}
			}
		}
		
		if (isLoadMore) {
			// Âä†ËΩΩÊõ¥Â§öÔºöËøΩÂä†Âà∞Áé∞ÊúâÂàóË°®
			qaPosts.value = [...qaPosts.value, ...newQaPosts]
			qaPage.value++
		} else {
			// È¶ñÊ¨°Âä†ËΩΩÊàñÂà∑Êñ∞ÔºöÊõøÊç¢ÂàóË°®
			qaPosts.value = newQaPosts
			qaPage.value = 2 // ‰∏ãÊ¨°Âä†ËΩΩÊõ¥Â§öÊó∂‰ªéÁ¨¨2È°µÂºÄÂßã
		}
		
		// Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
		qaHasMore.value = newQaPosts.length >= qaLimit.value
		
		// Ëß¶ÂèëËßÜÂõæÊõ¥Êñ∞
		qaPosts.value = qaPosts.value.slice()
	} catch (e) {
		console.error('Âä†ËΩΩÈóÆÁ≠îÊï∞ÊçÆÂ§±Ë¥•:', e)
		if (!isLoadMore) {
			qaPosts.value = []
		}
	} finally {
		qaLoading.value = false
	}
}

