async function toggleLike(post) {
	if (!currentUser.value) {
		uni.showToast({
			title: 'ËØ∑ÂÖàÁôªÂΩï',
			icon: 'none'
		})
		return
	}
	
	try {
		const result = await api.likeFeed(post.id)
		if (result) {
			// Êõ¥Êñ∞ÁÇπËµûÊï∞ÈáèÂíåÁä∂ÊÄÅ
			post.likes = result.likes
			post.isLiked = result.isLiked
			
			uni.showToast({
				title: post.isLiked ? 'Â∑≤ÁÇπËµû' : 'Â∑≤ÂèñÊ∂àÁÇπËµû',
				icon: 'none',
				duration: 1000
			})
		}
