// ÊêúÁ¥¢Â§ÑÁêÜ
function handleSearch() {
	if (!searchText.value.trim()) return
	
	isSearching.value = true
	if (topTab.value === 'square') {
		loadFeeds({ search: searchText.value.trim() })
	} else if (topTab.value === 'qa') {
		loadQuestions({ search: searchText.value.trim() })
	} else if (topTab.value === 'science') {
		loadArticles({ search: searchText.value.trim() })
	}
}

// ÊêúÁ¥¢ËæìÂÖ•Â§ÑÁêÜÔºàÈò≤ÊäñÔºâ
let searchTimeout = null
function handleSearchInput() {
	if (searchTimeout) {
		clearTimeout(searchTimeout)
	}
	
	searchTimeout = setTimeout(() => {
		if (searchText.value.trim()) {
			handleSearch()
		} else {
			// Â¶ÇÊûúÊêúÁ¥¢Ê°Ü‰∏∫Á©∫ÔºåÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
			if (topTab.value === 'square') {
				loadFeeds()
			} else if (topTab.value === 'qa') {
				loadQuestions()
			} else if (topTab.value === 'science') {
				loadArticles()
			}
			isSearching.value = false
		}
	}, 500) // 500ms Èò≤Êäñ
}

// Ê∏ÖÈô§ÊêúÁ¥¢
function clearSearch() {
	searchText.value = ''
	isSearching.value = false
	if (topTab.value === 'square') {
		loadFeeds()
	} else if (topTab.value === 'qa') {
		loadQuestions()
	} else if (topTab.value === 'science') {
		loadArticles()
	}
}
